
// -------------------------
// -------- Products--------
// -------------------------

.k-product {
	padding: 1.125rem;
	width: 100%;
	position: relative;

	&:hover {
		.product-image__img {
			opacity: 0;
			transform: scale(1.15);

			&.first {
				opacity: 1;
			}
		}

		.k-product__actions {
				opacity: 1;
				transform: translateX(5px);
				path {
					fill: black !important;
					color: black !important;
					transition: all 0.3s cubic-bezier(0.165, 0.84, 0.44, 1);
				}
				.btn-light.focus, .btn-light:focus, .btn-light:hover
				{
					background: transparent !important;
					border-color: transparent !important;

				path {
					fill: $accent-color !important;
					color: $accent-color !important;
				}
				}
		}

		.button-container{
			button{
				opacity: 1;
				transform: translateY(0);
			}
		}
	}

	&__img-container {
		margin-bottom: 1rem;
	}

	&__figure {
		margin-bottom: 0 !important;
		position: relative;
		overflow: hidden;
		padding: 0.5rem !important;
		-webkit-transition: all 0.3s ease-in-out;
		-moz-transition: all 0.3s ease-in-out;
		-o-transition: all 0.3s ease-in-out;
		transition: all 0.3s ease-in-out;
		background-color:#efefef !important;
	}

	.product-image__img {
		transform: scale(1);
		-webkit-transition: all 0.3s ease-in-out;
		-moz-transition: all 0.3s ease-in-out;
		-o-transition: all 0.3s ease-in-out;
		transition: all 0.3s ease-in-out;
		// &.first {
		// 	opacity: 0;
		// }

	}

	.k-product__actions { position: absolute;
		top: 0px;
		inset-inline-start: 0;
		opacity: 0;
		transform: translateX(-50%);
		-webkit-transition: all 0.5s ease-in-out;
		-moz-transition: all 0.5s ease-in-out;
		-o-transition: all 0.5s ease-in-out;
		transition: all 0.5s ease-in-out;
		// background-color: rgba(#fff, 0.6);
		margin-top: 0.25rem;
		
		svg,
		path {
			fill: #3d464d !important;
			color: #3d464d !important;
		}
	}

	.button-container{
		width: 100%;
		position: absolute;
		bottom: 0;
		left: 0;
		right: 0;
		display: flex;
		justify-content: center;
		button{
			width: 100%;
			opacity: 0;
			transform: translateY(10px);
			-webkit-transition: all 0.5s ease-in-out;
			-moz-transition: all 0.5s ease-in-out;
			-o-transition: all 0.5s ease-in-out;
			transition: all 0.5s ease-in-out;
			border-radius: 0;
		}
	}

	&__info {
		text-align: center;
	}

	&__title {
		margin-bottom: 0.5rem;
		a {
			font-size: 1.025rem;
			font-weight: 500;

			color: black !important;
			opacity: 1;
		}
	}

	&__rating {
		display: flex;
		align-items: center;
		justify-content: center;
		margin-bottom: 0.5rem;
		.review {
			font-size: 0.75rem;
			color: gray;
			margin-inline-start: 0.5rem;
		}
	}

	&__price {
		font-size: 1rem;
		font-weight: 500;
	}
}

// -------------------------
// --- Categories.
// -------------------------
$cm-category-box-shadow : 0 0 10px 2px rgba(0,0,0,0.2);

.cm-categories {
  .cm-category{
    text-align: center;
    margin-bottom: 1rem;
    background-color: white;
    padding: 0.5rem;
    box-shadow: 0, 0, 0m;
    -webkit-box-shadow: 0px 0px 4px 0px rgba(0,0,0,0.2);
    -moz-box-shadow: 0px 0px 4px 0px rgba(0,0,0,0.2);
    box-shadow: 0px 0px 4px 0px rgba(0,0,0,0.2);
    transition: box-shadow 0.3s ease-in-out;
    
    
    &:hover {
      box-shadow: $cm-category-box-shadow;
    }
    &__figure{
        height: 334px;
      img{
        width: 100%;
        height: 100%;
        object-fit: contain;
      }
    }
    &__title{
      font-size: 1.25rem;
      color: black;
    }

  }
}


.category-item{
	text-align: center;

	figure {
		width: 100%;
		margin-bottom: 0.5rem !important;
		padding-inline: 0.5rem;
    padding-top: 0.5rem;
		
		img{
		width: 100% !important;
		}
	}
	&__title {
		color: black;
		// color: #333;
		font-weight: 700;
		margin-bottom: 0.5rem;
	}
}



.color__item{
  color:gray;
  font-size: 0.75rem;
  padding:  0.01rem 0.5rem;
  border: 1px solid gray;
  border-radius: 2px;
  margin-inline-end: 0.5rem;
  cursor: pointer !important;
}

html[dir="rtl"]{
	.k-product {
			.k-product__actions {
				transform: translateX(50%);
			}
		&:hover {
			.k-product__actions {
					transform: translateX(-5px);
			}
		}
	}
}
// ---------------------------
// --- Single Product Details.
// ---------------------------
.single-product .slick-slide.slick-active.slick-current > div,
.single-product .slick-slide > div
{
	background: #efefef !important;
}
.product-gallery__carousel .slick-track {
 padding-block: 0.5rem;
}
.product-gallery__carousel-item.product-image.product-gallery__carousel-item--active
{
	box-shadow: 0 0  4px 0 #333 !important;
}

.indicator.indicator--trigger--click
{
	.indicator__value
	{
						transition: color 0.3s cubic-bezier(0.165, 0.84, 0.44, 1);
	}
	path {
						fill: current !important;
						color:current !important;
						transition: all 0.3s cubic-bezier(0.165, 0.84, 0.44, 1);
					}
		&:hover {
		circle,path{
			fill: $accent-color !important;
			color:$accent-color !important;
		}
		.indicator__value
		{
			background-color:$accent-color !important;

		}
	
	}
}

